<!--
 * @Descripttion: 体检报告样式组件
 * @Author: 曾元球
 * @Date: 2019-06-17 16:35:24
 * @LastEditors: 曾元球
 * @LastEditTime: 2019-08-12 09:05:59
 * @param: {Object} [user]
 * @param: {Array} [projectList]
 * @example: <medical-report-html :user='{Object}' :projectList='{Array}' />
 -->
<template>
  <div>
    <div class="print-page">
      <div class="print-body flex-v">
        <p class="text">
          体检流水号：{{user.examUserId}}，体检日期：{{user.examDate | formatDate('Y年M月D日')}}
        </p>
        <div class="avatar"
          style="height:180px;text-align:right">
          <img :src="user.addOn1"
            v-if="user.addOn1"
            width="150">
          <template v-else>
            <img v-if="user.sexType==2"
              src="@/assets/img/person-woman.png"
              width="150" />
            <img v-else
              src="@/assets/img/person-man.png"
              width="150">
          </template>
        </div>
        <div class="content flex-v">
          <div class="title">
            {{user.createOrganizationName}}<br>健康体检报告
          </div>
          <div class="logo flex-v-center">
            <img :src="user.createOrganizationIdLogo" />
          </div>
          <ul class="userinfo">
            <li><label>体检流水号</label><span>：{{user.examUserId}}</span></li>
            <li><label>身份证号</label><span>：{{idNo}}</span></li>
            <li><label>姓名</label><span>：{{user.userName}}</span></li>
            <li><label>性别</label><span>：{{user.sexType == 1 ? '男' : user.sexType == 2 ? '女' :　'不详'}}</span></li>
            <li><label>年龄</label><span>：{{user.age}}</span></li>
            <li><label>联系电话</label><span>：{{user.contactPhone}}</span></li>
            <li><label>体检日期</label><span>：{{user.examDate | formatDate}}</span></li>
            <li><label>现住址</label><span>：{{user.liveAddress}}</span></li>
          </ul>
        </div>
      </div>
    </div>
    <div class="print-page">
      <div class="print-body flex-v">
        <p class="text">
          体检流水号：{{user.examUserId}}，体检日期：{{user.examDate | formatDate('Y年M月D日')}}
        </p>
        <div class="content">
          <h3 class="title-item"
            style="margin:20px 0">项目目录</h3>
          <el-table :data="pList"
            style="min-width:960px;max-width:100%"
            :span-method='mergeMethod'
            border>
            <el-table-column label="科室"
              prop="deptName">
            </el-table-column>
            <el-table-column label="检查项目名称"
              prop="parentName">
            </el-table-column>
            <el-table-column label="检查小项名称"
              prop="pname">
            </el-table-column>
            <el-table-column label="是否检查"
              prop="isCheck"
              width="100">
              <template v-slot='scope'>
                {{scope.row.isCheck == 1 ? '已检' : '未检'}}
              </template>
            </el-table-column>
            <el-table-column label="检查医生"
              prop="oneAuthUserName">
            </el-table-column>
            <el-table-column label="体检日期"
              prop="updateDate"
              width="150">
              <template v-slot='scope'>
                {{scope.row.updateDate | formatDate}}
              </template>
            </el-table-column>
          </el-table>
        </div>
      </div>
    </div>
    <div class="print-page">
      <div class="print-body content-center"
        style="margin:20px;padding:0;overflow-x:hidden">
        <p class="text"
          style="width: 100%;text-align:left">
          体检流水号：{{user.examUserId}}，体检日期：{{user.examDate | formatDate('Y年M月D日')}}
        </p>
        <div class="content">
          <!-- maxProjectList projectList -->
          <h3 class="title-item">检查结论：</h3>
          <template v-if='user.examStatus==5'>
            <div style="margin-top:30px"
              v-for="item in maxProjectList"
              :key="item.projectId">
              <div class="text flex">
                <span class="icon">★</span>
                {{item.name}}：
                <pre class='flex-item'
                  style='white-space:pre-wrap;'>{{concatStr(item.smallProjectList, 'checkConclusion')}}</pre>
                <!-- {{item.name}}：<span v-for='items in item.smallProjectList'
                  :key="items.projectId">
                  {{items.checkConclusion}}
                </span>&nbsp; -->
              </div>
              <div class="min-text flex">
                诊断意见或印象：
                <pre class='flex-item'
                  style='white-space:pre-wrap;'>{{concatStr(item.smallProjectList, 'checkProposal')}}</pre>
                <!-- <span v-for='items in item.smallProjectList'
                  :key="items.projectId">{{items.checkProposal}}
                </span>&nbsp; -->
              </div>
            </div>
          </template>
          <div class="dotted-line"></div>
          <h3 class="title-item">温馨提示：</h3>
          <ol class="ol-list"
            v-if='user.examStatus==5'>
            <template v-for="item in projectList">
              <li :key="item.projectId"
                v-if="item.checkWaring">{{item.checkWaring}}</li>
            </template>
          </ol>
          <div class="tfoot"
            style="font-size:14px;border-bottom:0 none">
            检查日期：{{(user.examStatus == 5 && user.twoAuthUserTime) | formatDate}}
            &nbsp;&nbsp;
            检查医生：{{user.twoAuthUserName | emptyData}}
          </div>
        </div>
      </div>
    </div>

    <div class="print-page">
      <div class="print-body flex-v">
        <p class="text">
          体检流水号：{{user.examUserId}}，体检日期：{{user.examDate | formatDate('Y年M月D日')}}
        </p>
        <div class="content">
          <div v-for="item in maxProjectList"
            :key="item.id">
            <h3 class="title-item">{{item.name}}</h3>
            <!-- 1 为数值型 -->
            <div class="table-border"
              v-if="item.type==1">
              <el-table :data='item.smallProjectList'>
                <el-table-column label='序号'
                  type='index'></el-table-column>
                <el-table-column label="项目名称"
                  prop="pname">
                </el-table-column>
                <el-table-column label="检查结果"
                  prop="checkValue">
                </el-table-column>
                <el-table-column label="单位"
                  prop="projectSymbol"
                  width="100">
                </el-table-column>
                <el-table-column label="参考值"
                  prop="rule"
                  width="160">
                </el-table-column>
                <el-table-column label="提示"
                  width="60">
                  <template v-slot='scope'>
                    <i class="el-icon-bottom"
                      style="font-size:20px;color:#5AD4BB"
                      v-show="isNormal(scope.row)===-1"></i>
                    <i class="el-icon-top"
                      style="font-size:20px;color:#FE6F71"
                      v-show="isNormal(scope.row)===1"></i>
                  </template>
                </el-table-column>
              </el-table>
              <div class="info flex">
                小结：
                <pre class='flex-item'
                  style='white-space:pre-wrap;'>{{concatStr(item.smallProjectList, 'checkConclusion')}}</pre>
              </div>
              <div class="tfoot">
                检查日期：{{item.oneAuthUserTime | formatDate}}
                &nbsp;&nbsp;
                检查医生：{{item.oneAuthUserName | emptyData}}
              </div>
            </div>

            <!-- 2 文本医技 -->
            <div class="text-2"
              v-if="item.type==2"
              style="width:960px;max-width:100%">
              <div class="text-2-box">
                <div class="flex">
                  <label>检查部位：</label>
                  <div class="flex-item">
                    {{concatStr(item.smallProjectList, 'checkPart', '、')}}
                  </div>
                </div>
                <div class="flex"
                  style="margin-top:20px">
                  <label>检查结论：</label>
                  <div class="flex-item"
                    style='overflow:hidden'>
                    <pre style='white-space:pre-wrap;'>{{concatStr(item.smallProjectList, 'checkConclusion')}}</pre>
                  </div>
                </div>
              </div>
              <div class="tfoot">
                检查日期：{{item.oneAuthUserTime | formatDate}}
                &nbsp;&nbsp;
                检查医生：{{item.oneAuthUserName | emptyData}}
              </div>
            </div>

            <!-- 3 文本科室 -->
            <div class="table-border"
              v-if="item.type==3">
              <el-table :data='item.smallProjectList'>
                <el-table-column label='序号'
                  type='index'></el-table-column>
                <el-table-column label="项目名称"
                  prop="pname">
                </el-table-column>
                <el-table-column label="检查结果"
                  prop="checkValue">
                </el-table-column>
              </el-table>
              <div class="tfoot">
                检查日期：{{item.oneAuthUserTime | formatDate}}
                &nbsp;&nbsp;
                检查医生：{{item.oneAuthUserName | emptyData}}
              </div>
            </div>

          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import jsBarcode from 'jsbarcode'

export default {
  name: 'MedicalReportHtml',
  props: {
    user: {
      type: Object,
      default() {
        return {}
      }
    },

    projectList: {
      type: Array,
      default: () => []
    }
  },

  methods: {
    mergeMethod({ row, column, rowIndex, columnIndex }) {
      if (columnIndex === 0) {
        if (row.rowSpan) return [row.rowSpan, 1]
        return [0, 0]
      }
    },

    // 检测值是否正常
    isNormal({ rule, checkValue }) {
      let nCheckVal = Number(checkValue)
      if (checkValue === '' || isNaN(nCheckVal)) return 0
      let [bottom, top] = rule.split(' - ')
      let nBottom = bottom ? Number(bottom) : ''
      let nTop = top ? Number(top) : ''
      if (nBottom !== '' && nCheckVal < nBottom) {
        return -1 // 偏低
      }
      if (nTop !== '' && nCheckVal > nTop) {
        return 1 // 偏高
      }
      return 0
    },

    handleCheckPart(list) {
      return list.map(t => t.checkPart).join('、')
    },

    concatStr(list, key, delimiter = ' ') {
      return list.map(item => item[key]).join(delimiter)
    }
  },

  computed: {
    maxProjectList() {
      if (!this.projectList.length) return []
      let tempObj = {}
      this.projectList.forEach(item => {
        if (tempObj[item.parentId]) {
          tempObj[item.parentId].smallProjectList.push(item)
        } else {
          tempObj[item.parentId] = {
            id: item.parentId,
            name: item.parentName,
            type: item.resultType,
            oneAuthUserName: item.oneAuthUserName,
            oneAuthUserTime: item.oneAuthUserTime,
            smallProjectList: [item]
          }
        }
      })
      return Object.keys(tempObj).map(item => tempObj[item])
    },

    pList() {
      if (!this.projectList.length) return []
      let aTemp = this.projectList
        .sort((current, next) => {
          return current.parentId - next.parentId
        })
        .sort((current, next) => current.deptId - next.deptId)
      let oTemp = {}
      aTemp.forEach(it => {
        if (oTemp[it.deptId]) oTemp[it.deptId]++
        else oTemp[it.deptId] = 1
      })
      Object.keys(oTemp).forEach(k => {
        for (let i = 0, t; (t = aTemp[i++]); ) {
          if (t.deptId === k) {
            t.rowSpan = oTemp[k]
            break
          }
        }
      })
      return aTemp
    },

    idNo() {
      let id = this.user.idNo
      if (!id) return ''
      let idStart = id.slice(0, 6)
      let idEnd = id.slice(-4)
      return `${idStart}********${idEnd}`
    }
  }
}
</script>

<style lang="less" scoped>
.print-page {
  page-break-after: always;
  border-top: 1px solid @default-border;
  &:first-child {
    margin-top: -1px;
    .content {
      padding-top: 80px;
    }
  }
}
.print-body {
  padding: 20px;
}
.content {
  margin: 0 auto;
  padding: 50px 0;
}
.title {
  text-align: center;
  font-family: '楷体';
  font-size: 40px;
  font-weight: 700;
  color: #000;
}
.logo {
  margin: 120px auto 60px;
  width: 180px;
  height: 180px;
  border-radius: 50%;
  img {
    width: 100%;
  }
}
.userinfo {
  margin: 0 auto;
  font-size: 20px;
  label,
  span {
    display: inline-block;
    vertical-align: top;
    line-height: 1.2;
  }
  label {
    width: 5em;
    text-align: justify;
    text-align-last: justify;
    &:after {
      content: '';
      display: inline-block;
      width: 100%;
    }
  }
  span {
    padding-right: 3px;
    margin-left: 1em;
    width: 240px;
    text-indent: -1em;
    border-bottom: 1px solid #444353;
  }
}
.content-center {
  text-align: center;
  font-size: 0;
  & > .content {
    display: inline-block;
    min-width: 720px;
    max-width: 960px;
    box-sizing: content-box;
    text-align: left;
  }
}
h3.title-item {
  font-size: 24px;
  font-weight: 700;
}
.text {
  font-size: 16px;
}
.icon {
  margin-right: 5px;
  color: @color-red;
}
.min-text {
  margin-top: 20px;
  font-size: 14px;
}
.dotted-line {
  margin: 30px 0 30px -50%;
  width: 200%;
  border-top: 1px dashed #eee;
}
.ol-list {
  margin: 20px 0 0 1em;
  list-style-type: decimal;
  font-size: 14px;
  li {
    padding-bottom: 10px;
  }
}

.table-border {
  margin: 20px 0;
  width: 960px;
  max-width: 100%;
  border: 1px solid #ebeef5;
  border-bottom: 0;
}
/deep/ .el-table {
  width: 960px;
  max-width: 100%;
  &:before {
    width: 0;
    height: 0;
  }
  th {
    background: #f3f3f3 !important;
  }
  th,
  td {
    text-align: center !important;
  }
}
.text-2 {
  margin: 20px auto;
  border: 1px solid #ebeef5;
  border-bottom: 0 none;
  &-box {
    padding: 20px 50px 50px;
    border-bottom: 1px solid #ebeef5;
  }
}
.info {
  padding: 20px;
  border-bottom: 1px solid #ebeef5;
}
.tfoot {
  padding: 16px 20px;
  text-align: right;
  border-bottom: 1px solid #ebeef5;
}
</style>


