<template>
  <div>
    <medical-report-html :user='user'
      :projectList='projectList' />
  </div>
</template>

<script>
import printMethod from '@/lib/print-method'
import MedicalReportHtml from '@/components/MedicalReportHtml'
export default {
  name: 'previewReport',
  components: {
    MedicalReportHtml
  },
  props: {
    examUserId: {
      type: String,
      required: true
    },
    showPreview: Boolean
  },
  data() {
    return {
      user: {},
      projectList: []
    }
  },

  methods: {
    getExamReport(examUserId) {
      this.$post('/user/examUser/showReportExam', {
        examUserId
      })
        .then(({ data }) => {
          this.user = data.user
          this.projectList = data.smallProjectList
        })
        .catch(err => {
          this.$message.error(err)
        })
    },

    printPage() {
      this.$confirm('确定打印视为打印一次！', {
        title: '温馨提示',
        showClose: false,
        customClass: 'msg-box'
      })
        .then(() => {
          let loading = this.$loading({ text: '打印准备中...' })
          this.$post('/user/examUser/printRecord', {
            idList: this.examUserId
          })
            .then(() => {
              console.log('统计打印成功')
              printMethod(this.$el, '体检报告')
              setTimeout(() => {
                loading.close()
              }, 800)
            })
            .catch(err => {
              console.log('打印记录失败，无法打印！', err)
              loading.close()
              this.$message.error('打印记录失败，无法打印！')
            })
        })
        .catch(cancel => cancel)
    }
  },

  watch: {
    examUserId: {
      handler(val) {
        if (val === '') return
        this.getExamReport(val)
      },
      immediate: true
    }
  }
}
</script>

<style lang="less" scoped>
</style>


